.navbar {
  display: flex;
  background-color: black;
  color: white;
  width: 100%;
  margin-top: 0;
  margin-bottom: 5vh;
}

.nav-brand {
  margin: 0 3vw;
  cursor: pointer;
}

.navbar-list {
  margin: 0 auto;
  display: flex;
  justify-self: flex-end;
  cursor: pointer;
}

.navbar-list-item {
  list-style-type: none;
  padding: 19px 20px;
}
form{
  padding: 19px 20px;

}
.nav-link{
  color:white;
  text-decoration:none;
  cursor:pointer;
}

@media(max-width:760px){
  .navbar {
  display: grid;
  grid-template-columns:1fr;
}
.navbar-list {
display:block;
}
}